export default () => {
  const logModalOpenBtn = document.querySelector('.js--log-modal-btn');
  const logModalContainer = document.querySelector('.js--log-modal-overlay');
  const logModal = document.querySelector('.js--log-modal');
  const logModalCloseBtn = document.querySelector('.js--log-modal__close-btn');

  function openMenu() {
    logModalContainer.classList.add('active');
    logModal.classList.add('active');
  }

  function closeMenu() {
    logModalContainer.classList.remove('active');
    logModal.classList.remove('active');
  }

  logModalOpenBtn.addEventListener('click', openMenu);
  logModalCloseBtn.addEventListener('click', closeMenu);
  logModalContainer.addEventListener('click', closeMenu);
};
