import anime from 'animejs';
// import Swiper, { EffectFade } from 'swiper';

export default () => {
  // Containers
  const startScreen = document.querySelector('.js--start-screen');
  const chooseScreen = document.querySelector('.js--choose-screen');
  const jsWinnerScreen = document.querySelector('.js--js-winner-screen');
  const userWinnerScreen = document.querySelector('.js--user-winner-screen');

  console.log(startScreen, chooseScreen, jsWinnerScreen, userWinnerScreen);

  const ChooseBtns = document.querySelectorAll('.js--choose-btn');
  const playAgainbtn = document.querySelectorAll('.js--play-again');
  const userHand = document.querySelector('.js--user-hand');
  const jsHand = document.querySelector('.js--js-hand');
  const startBtn = document.querySelector('.js--start-btn');

  // const sliderContainer = new Swiper('.swiper', {
  //   modules: [EffectFade],
  //   allowTouchMove: false,
  //   slidesPerView: 1,
  //   effect: 'fade',
  //   fadeEffect: {
  //     crossFade: true,
  //   },
  // });

  startBtn.addEventListener('click', () => {
    startScreen.classList.remove('visible');
    setTimeout(() => {
      startScreen.style.display = 'none';
      chooseScreen.style.display = 'block';
    }, 600);
    // chooseScreen.style.display = 'block';
    setTimeout(() => {
      chooseScreen.classList.add('visible');
    }, 800);
    // sliderContainer.slideNext(300);
  });

  function toWinnersBlock(prevContainer, winner) {
    prevContainer.classList.remove('visible');
    setTimeout(() => {
      prevContainer.style.display = 'none';
      winner.style.display = 'block';
    }, 600);
    setTimeout(() => {
      winner.classList.add('visible');
    }, 600);
  }

  const arrayGame = ['rock', 'scissors', 'paper'];
  const objAssociation = {
    'scissors': 'rock',
    'rock': 'paper',
    'paper': 'scissors',
  };

  const objUserHands = {
    'defaultHand': 'img/user-hands/user-hand-default.png',
    'scissors': 'img/user-hands/user-hand-scissors.png',
    'rock': 'img/user-hands/user-hand-rock.png',
    'paper': 'img/user-hands/user-hand-paper.png',
  };

  const objJsHands = {
    'defaultHand': 'img/js-hands/js-hand-default.png',
    'scissors': 'img/js-hands/js-hand-scissors.png',
    'rock': 'img/js-hands/js-hand-rock.png',
    'paper': 'img/js-hands/js-hand-paper.png',
  };

  const animationOfUserHand = anime({
    targets: '.js--user-hand',
    keyframes: [
      {rotate: -40},
      {rotate: 20},
      {rotate: -40},
      {rotate: 20},
      {rotate: -40},
      {rotate: 20},
      {rotate: -220},
      {rotate: 0},
    ],
    duration: 2200,
    autoplay: false,
    easing: 'spring(1, 80, 10, 0)',
  });

  const animationOfJsHand = anime({
    targets: '.js--js-hand',
    keyframes: [
      {rotate: 137},
      {rotate: -20},
      {rotate: 40},
      {rotate: -20},
      {rotate: 40},
      {rotate: -20},
      {rotate: 31},
      {rotate: 0},
    ],
    autoplay: false,
    duration: 2200,
    direction: 'reverse',
    easing: 'spring(1, 80, 10, 0)',
  });

  const animationOfConfetti = anime({
    targets: '#confetti-svg .lines path',
    keyframes: [
      {
        translateX: 110,
      },
      {
        translateX: -100,
      },
      {
        translateX: 100,
      },
      {
        translateX: 110,
      },
      {
        translateX: 110,
      },
      {
        translateX: -100,
      },
      {
        translateX: 100,
      },
      {
        translateX: 110,
      },
    ],
    duration: 3600,
    autoplay: false,
    easing: 'cubicBezier(.5, .05, .1, .3)',
    loop: false,
  });

  const animationOfConfetti2 = anime({
    targets: '#confetti-svg .greens path',
    keyframes: [
      {
        translateX: 90,
      },
      {
        translateX: -20,
      },
      {
        translateX: 110,
      },
      {
        translateX: -50,
      },
      {
        translateX: 90,
      },
      {
        translateX: -20,
      },
      {
        translateX: 110,
      },
      {
        translateX: -50,
      },
    ],
    duration: 8000,
    autoplay: false,
    easing: 'cubicBezier(.5, .05, .1, .3)',
    loop: false,
  });

  const animationOfConfetti3 = anime({
    targets: '#confetti-svg .black path',
    keyframes: [
      {
        translateX: 30,
      },
      {
        translateX: -100,
      },
      {
        translateX: 110,
      },
      {
        translateX: -50,
      },
      {
        translateX: 30,
      },
      {
        translateX: -100,
      },
      {
        translateX: 110,
      },
      {
        translateX: -50,
      },
    ],
    duration: 7200,
    autoplay: false,
    easing: 'cubicBezier(.5, .05, .1, .3)',
    loop: false,
  });

  const animationOfConfetti4 = anime({
    targets: '#confetti-svg .blue path',
    keyframes: [
      {
        translateX: 10,
      },
      {
        translateX: -150,
      },
      {
        translateX: 60,
      },
      {
        translateX: -90,
      },
      {
        translateX: 10,
      },
      {
        translateX: -150,
      },
      {
        translateX: 60,
      },
      {
        translateX: -90,
      },
    ],
    duration: 6400,
    autoplay: false,
    easing: 'cubicBezier(.5, .05, .1, .3)',
    loop: false,
  });

  const animationOfConfetti5 = anime({
    targets: '#confetti-svg',
    keyframes: [
      {
        translateY: 720,
        opacity: 0,
      },
    ],
    duration: 7400,
    autoplay: false,
    easing: 'cubicBezier(.5, .05, .1, .3)',
    loop: false,
  });

  function confettiAnimationInit() {
    animationOfConfetti.play();
    animationOfConfetti2.play();
    animationOfConfetti3.play();
    animationOfConfetti4.play();
    animationOfConfetti5.play();
  }

  const animationOfOpacityHands = anime({
    targets: [document.querySelectorAll('.js--hand')],
    opacity: [
      {value: 0},
      {value: 1},
    ],
    duration: 300,
    autoplay: false,
    easing: 'spring(1, 80, 10, 0)',
  });

  let jsChoice;
  let scoreJs = 0;
  let scoreUser = 0;
  let rounds = 1;

  function getRandomHand(arr) {
    const count = arr.length;
    const result = Math.round(Math.random() * (count - 1));
    jsChoice = arrayGame[result];
  }

  function changeImages(hand, handChoisen, defaultHand) {
    hand.src = defaultHand;
    setTimeout(() => {
      animationOfOpacityHands.play();
      setTimeout(() => {
        hand.src = handChoisen;
      }, 500);
    }, 1680);
  }

  function createLogElement(number, user, js) {
    const container = document.querySelector('.js--log-items-container');

    const list = document.createElement('ul');
    list.classList.add('log__row');

    const elemNumber = document.createElement('li');
    elemNumber.classList.add('log__text', 'log__item--number');
    elemNumber.textContent = number;

    const elemUser = document.createElement('li');
    elemUser.classList.add('log__text', 'log__item--user');
    elemUser.textContent = user;

    const elemJs = document.createElement('li');
    elemJs.classList.add('log__text', 'log__item--js');
    elemJs.textContent = js;

    list.append(elemNumber);
    list.append(elemUser);
    list.append(elemJs);

    container.append(list);
  }

  function clearLog() {
    const container = document.querySelector('.js--log-items-container');
    const elemsOfLog = container.querySelectorAll('.log__row');
    elemsOfLog.forEach((elem) => {
      elem.remove();
    });
    rounds = 1;
  }

  const layoutScore = document.querySelectorAll('.js--score');

  function initGame(chioce) {
    getRandomHand(arrayGame);
    const userChioce = chioce.game.toString();
    const userHandChoisen = objUserHands[userChioce];
    const userHandDefault = objUserHands['defaultHand'];
    const jsHandDefault = objJsHands['defaultHand'];
    const jsHandChoisen = objJsHands[jsChoice];

    changeImages(jsHand, jsHandChoisen, jsHandDefault);
    changeImages(userHand, userHandChoisen, userHandDefault);

    if (jsChoice === userChioce) {
      console.log(`Ничья, Пользователь - ${userChioce} JS - ${jsChoice}`);
      createLogElement(rounds, userChioce, jsChoice);
      rounds++;

    } else {
      const userOpposite = objAssociation[userChioce];
      const jsOpposite = objAssociation[jsChoice];
      console.log(userChioce, userOpposite, 'user');
      console.log(jsChoice, jsOpposite, 'js');

      if (userChioce === jsOpposite) {
        console.log(`Пользователь - победил ${userChioce} бьют ${jsChoice}`);
        scoreUser = scoreUser + 1;

        setTimeout(() => {
          layoutScore.forEach((score) => {
            score.innerHTML = `${scoreUser}<span>:</span>${scoreJs}`;
          });
        }, 2300);

        console.log(`счет user ${scoreUser} : js ${scoreJs}`);
        createLogElement(rounds, userChioce, jsChoice);
        rounds++;

      } else if (jsChoice === userOpposite) {
        console.log(`js - победил ${jsChoice} бьют ${userChioce}`);
        scoreJs = scoreJs + 1;
        createLogElement(rounds, userChioce, jsChoice);
        rounds++;

        setTimeout(() => {
          layoutScore.forEach((score) => {
            score.innerHTML = `${scoreUser}<span>:</span>${scoreJs}`;
          });
        }, 2300);

        console.log(`счет user ${scoreUser} : js ${scoreJs}`);
      }
    }

    if (scoreUser === 3) {
      console.log(`Пользователь победил со счетом ${scoreUser} : js ${scoreJs}`);
      setTimeout(() => {
        toWinnersBlock(chooseScreen, userWinnerScreen);
        confettiAnimationInit();
      }, 3000);

    } else if (scoreJs === 3) {
      console.log(`JS победил со счетом ${scoreJs} : js ${scoreUser}`);
      setTimeout(() => {
        toWinnersBlock(chooseScreen, jsWinnerScreen);
        confettiAnimationInit();
      }, 3000);
    }
  }

  ChooseBtns.forEach((el) => {
    el.addEventListener('click', (e) => {
      const target = e.target.closest('.js--choose-btn');
      const userChioce = target.dataset;

      animationOfUserHand.play();
      animationOfJsHand.play();
      initGame(userChioce);
    });
  });

  playAgainbtn.forEach((button) => {
    button.addEventListener('click', () => {
      const parent = button.closest('.container');
      console.log(parent);
      scoreJs = 0;
      scoreUser = 0;

      setTimeout(() => {
        layoutScore.forEach((score) => {
          score.innerHTML = `${scoreUser}<span>:</span>${scoreJs}`;
        });
      }, 2300);

      clearLog();
      toWinnersBlock(parent, startScreen);
    });
  });
};
